// Color system - Additional Theme colors
$c4g-blue: #02A1DB;
$c4g-blue-dark: #0e4268;
$c4g-grey: #6c757d;

// detail page - hero
$c4g-detail-hero-filter-color: rgba(#333, .3);
$c4g-detail-hero-title-color: #fff;
$c4g-detail-hero-text-shadow: 0 0 10px #000;
$c4g-detail-hero-text-intro-color: #fff;

// topbar
$c4g-topbar-bgcolor: #fff;

$primary: $c4g-blue;
$secondary: $c4g-blue-dark;
//$success: $green;
//$info: $cyan;
//$warning: $yellow;
//$danger: $red;
//$light: $gray-100;
//$dark: $gray-800;

$theme-colors: ();
$theme-colors: map-merge(
                (
                        "primary": $primary,
                        "secondary": $secondary
                ),
                $theme-colors
);

$c4gprops: ();
$c4gprops: map-merge(
                (
                        "c4g-blue":$c4g-blue,
                        "c4g-blue-dark":$c4g-blue-dark,
                        "c4g-grey":$c4g-grey,
                        "c4g-topbar-bgcolor": $c4g-topbar-bgcolor,
                        "c4g-detail-hero-filter-color":$c4g-detail-hero-filter-color,
                        "c4g-detail-hero-title-color":$c4g-detail-hero-title-color,
                        "c4g-detail-hero-text-shadow":$c4g-detail-hero-text-shadow,
                        "c4g-detail-hero-text-intro-color":$c4g-detail-hero-text-intro-color
                ),
                $theme-colors
);

// Body
//
// Settings for the `<body>` element.

$body-bg: #fff;
$body-color: $c4g-grey;

// Grid containers
//
// Define the maximum width of `.container` for different screen sizes.

$container-max-widths: (
  //sm: 576px,
  //md: 768px,
  //lg: 992px,
        xl: 1200px
);

// Components
//
// Define common padding and border radius sizes and more.

$line-height-lg: 1.5;
$line-height-sm: 1.5;

// Typography
//
// Font, line-height, and color for body text, headings, and more.

// stylelint-disable value-keyword-case
//$font-family-sans-serif: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
$font-family-sans-serif: "Source Sans Pro", sans-serif;
$font-family-monospace: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
$font-family-base: $font-family-sans-serif;

$font-size-base: 1rem; // Assumes the browser default, typically `16px`
$font-size-lg: $font-size-base * 1.25;
$font-size-sm: $font-size-base * .875;

$font-weight-lighter: lighter;
$font-weight-light: 300;
$font-weight-normal: 400;
$font-weight-bold: 700;
$font-weight-bolder: bolder;

$font-weight-base: $font-weight-normal;
$line-height-base: 1.5;

$h1-font-size: $font-size-base * 2.5;
$h2-font-size: $font-size-base * 2;
$h3-font-size: $font-size-base * 1.75;
$h4-font-size: $font-size-base * 1.5;
$h5-font-size: $font-size-base * 1.25;
$h6-font-size: $font-size-base;

$spacer: 1rem;
$headings-margin-top: $spacer / 2;
$headings-margin-bottom: $spacer / 2;
$headings-font-family: null;
$headings-font-weight: 500;
$headings-line-height: 1.5;
$headings-color: null;

// ============

// fonts
@import "fonts_custom";

@import "../../../schmiederohling/scss/bootstrap460/variables";
@import "variables_gutes";




