.c4g-modal-outer {
  position: fixed;
  z-index: 100;
  padding-top: 100px;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
}

.c4g-modal-inner {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: block;
  justify-content: center;
  min-width: 32em;
  min-height: 100px;
  max-width: 100%;
  padding: 1.25em;
  border: none;
  border-radius: 5px;
  background: #fff;
  font-family: inherit;
  max-height: 80%;
  overflow: auto;
}

.c4g-modal-hide {
  display: none;
}

.c4g-modal-content {
  color: #2d2d2d;
  font-weight: 400;
  line-height: normal;
  text-align: center;
  word-wrap: break-word;
  background-color: rgba(0, 0, 0, 0);

  h1, h2, h3, h4 {
    font-size: 2rem;
  }

  > div {
    margin-top: 5px;
    margin-bottom: 10px;
  }

  .title {
    padding: 0 1rem;
    font-weight: bold;
    font-size: 1.5rem;
  }

