  .detail-view {
    &__display-type,
    &__map,
    &__contact-wrapper {
      display: none;
    }

    &__price {
      text-align: center;
      margin-top: 1rem;
    }

    &__taxnote {
      font-size: .8rem;
      margin-bottom: 1rem;
      color: #6c757d;
    }

    &__description,
    &__image-gallery {
      flex: 0 0 100%;
      max-width: 100%;
    }
  }

  .field-description {
    font-size: 0.8rem;
    margin-bottom: 2rem;
  }

  .detail-field-wrapper {
    display: none;
  }

  .c4g-form-field {
    margin-bottom: .5rem;
  }

  input {
    @extend .form-control;
  }

  button {
    @extend .c4g-btn;
    @extend .c4g-btn-sm;
    @extend .c4g-btn-default;

    &.highlight {
      @extend .c4g-btn-primary;
      margin-right: 1rem;
    }

  }
}
